<template>
  <div>
       <el-breadcrumb separator-class="el-icon-arrow-right" style="line-height:40px;height:40px;background:#f8f8f8;border-radius:5px;padding-left:10px;margin-bottom:10px;">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{name:'adminbloglist'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <errorVue :errortitle="errortitle" :errordesc="errordesc" v-if="errshow"></errorVue>
    <el-form ref="form"  label-width="150px" style="width:60%;">
        <el-form-item label="">
            <el-input v-model="title" placeholder="100"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input type="textarea"  placeholder="255" v-model="desc" :autosize="{ minRows: 2,maxRows:5}"></el-input>
        </el-form-item>
        <el-form-item label="">
           <!--editordiv-->
          <ueVue :defaultMsg=content :config=config ref="ue"></ueVue>
           <!-- <el-input type="textarea" v-model="content" :autosize="{ minRows: 4,maxRows:10}"></el-input> -->
        </el-form-item>
    <el-form-item label="">
      <el-select v-model="id" placeholder="">
        <el-option
          v-for="btype in blogtypelist"
          :key="btype.id"
          :label="btype.name"
          :value="btype.id">
        </el-option>
      </el-select>
    </el-form-item>
      <div class="block">
   <el-form-item label="">
       <el-date-picker
      v-model="createtime"
      type="datetime"
      placeholder=""
      default-time="12:00:00">
    </el-date-picker>
        </el-form-item>
   
  </div>
        <el-form-item label="">
            <el-switch v-model="hiddenthis"></el-switch>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="save"></el-button>
        </el-form-item>
    </el-form>
  </div>
</template>
<script>
import UEVue from '../ue.vue';

import {
  formatDate,
  checkServercode,
  catcherror
} from "../../assets/js/commonFun";
export default {
  data() {
    return {
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      title: "",
      desc: "",
      content: "",
      createtime: "",
      hiddenthis: false,
      id: "", //
      blogtypelist: [], //
      errshow: false,
      errortitle: "",
      errordesc: ""
    };
  },
  components:{
    ueVue:UEVue,
  },
  created() {
    //
    var querystr = "?page=1&pagesize=10000";
    this.$axios
      .get("/admin/article/typelist" + querystr)
      .then(res => {
        if (checkServercode(res, this)) {
          //
          this.blogtypelist = res.data.result;
        }
      })
      .catch(err => {
        catcherror(this, "", err);
      });
    var id = this.$route.query.id;
    if (id) {
      //
      this.$axios
        .get("/admin/article/getblogbyid?id=" + id)
        .then(res => {
          if (checkServercode(res, this)) {
            var data = res.data.result[0];
            this.title = data.title;
            this.desc = data.description;
            this.content = data.content; //
            this.hiddenthis = data.ishidden == 1 ? true : false;
            let tagid = Number(res.data.message[0].tag_id); //int
            this.createtime = formatDate(
              new Date(data.createtime),
              "yyyy-MM-dd HH:mm:ss"
            );
            this.id = tagid;
          }
        })
        .catch(err => {
          catcherror(this, "", err);
        });
    } else {
      //
      this.$axios
        .get("/admin/islogin")
        .then(res => {
          checkServercode(res, this);
        })
        .catch(err => {
          catcherror(this, "", err);
        });
    }
  },
  methods: {
    //
    save() {
      var msg = "";
      let content = this.$refs.ue.getUEContent();
      if (this.title == "") {
        msg = "";
      } else if (this.title.length > 100) {
        msg = "100";
      } else if (this.desc == "") {
        msg = "";
      } else if (this.desc.length > 255) {
        msg = "255";
      } else if (content == "") {
        msg = "";
      } else if (this.id == "") {
        msg = "";
      }
      if (msg != "") {
        this.errortitle = "";
        this.errordesc = msg;
        this.errshow = true;
        return false;
      }

      var url = "/admin/article/edit";
      var hthis = this.hiddenthis == true ? 1 : 0; //
      if (this.$route.query.id) {
        //
        url += "?id=" + this.$route.query.id;
      }
      var postobj = {
        title: this.title,
        desc: this.desc,
        content: content,
        hiddenthis: hthis,
        blogtype: this.id
      };
      if (this.createtime != "") {
        //
        postobj.createtime = this.createtime;
      }

      this.$axios
        .post(url, postobj)
        //  this.$axios.post(url,
        //  'title='+this.title+"&desc="+this.desc+"&content="+content+"&hiddenthis="+hthis+"&blogtype="+this.id)
        .then(res => {
          if (checkServercode(res, this)) {
            //,
            this.$router.push({
              name: "adminbloglist"
            });
          }
        })
        .catch(err => {
          catcherror(this, "", err);
        });
      //
    }
  }
};
</script>
<style>

</style>



